.results {
  margin: 0 auto;
  margin-top: 40px;
  max-width: 1280px;
}

.results__container {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  width: calc(100% - 70*2px);
}

.results__title {
  margin: 0 auto;
  color: #FFF;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.8px;
}

.results__subtitle {
  margin: 0 auto;
  margin-top: 26px;
  color: #FFF;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
}

.results__videos {
  margin: 0 auto;
  margin-top: 20px;
  height: 150px;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: grey;
  list-style-type: none;
  padding: 0;
}

.results__video{
  list-style-type: none;
}

@media screen and (max-width: 830px) {
  .results__title {
    width: calc(100% - 50*2px);
  }

  .results__subtitle {
    width: calc(100% - 50*2px);
    margin-top: 22px;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
  }

  .results__videos {
    width: calc(100% - 50*2px);
  }
}

@media screen and (max-width: 530px) {
  .results__title {
    width: calc(100% - 18*2px);
  }

  .results__subtitle {
    width: calc(100% - 18*2px);
  }

  .results__videos {
    width: calc(100% - 18*2px);
  }
}