.menu{
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 50px;
  left: 0;
  transform: translate(-130%);
  transition: all 0.2s;
}

.menu.active{
  transform: translate(0);
}

.blur{
  width: 100vw;
  height: 100vh;
  left: 30%;
  backdrop-filter: blur(2px);
  position: absolute;
}

.menu__content{
  width: 30%;
  height: 100%;
  background-color: black;
  display: flex;
  flex-direction: column;
  transition: all 0.4s;
}

.menu__header{
  font-size: 2rem;
  color: white;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid white;
  width: 100%;
  text-align: center;
}

.menu__items, .menu__item, a{
  color: white;
  text-decoration: none;
  font-size: 1.3rem;
}

.menu__items, .menu__item{
  display: flex;
  align-items: center;
  margin: 10px 0;
}

.menu__items{
  padding: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: start;
  margin-left: 30px;
}

.menu__item:hover{
  opacity: 0.5;
}

@media screen and (max-width: 760px) {
  .menu__content{
    width: 100%;
  }

  .blur{
    backdrop-filter: none;
  }

}